.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
  position: absolute;
  opacity: 0.6;
  animation: changeImage 25s linear infinite;
  width: 100%;
  height: 100vh;
}

@keyframes changeImage {
  0% {
    background-image: url("../assets/backgrounds/b-1.jpg");
  }

  25% {
    background-image: url("../assets/backgrounds/b-2.jpg");
  }

  50% {
    background-image: url("../assets/backgrounds/b-3.jpg");
  }

  75% {
    background-image: url("../assets/backgrounds/b-4.jpg");
  }

  100% {
    background-image: url("../assets/backgrounds/b-5.jpg");
  }
}
